<template>
  <div class="jobs-page">
    <section class="py-5">
      <div class="container">
        <h2 class="fw-bold mb-4">在招职位</h2>
        <div class="jobs-search-bar mb-3">
          <select v-model="category" class="jobs-category-select">
            <option value="">全部类别</option>
            <option value="研发类">研发类</option>
            <option value="销售类">销售类</option>
            <option value="客户类">客户类</option>
          </select>
          <input v-model="search" @keyup.enter="doSearch" type="text" class="jobs-search-input" placeholder="输入职位关键词" />
          <button class="jobs-search-btn" @click="doSearch">搜索</button>
        </div>
        <div class="jobs-table-wrapper">
          <div class="jobs-table-header">
            <div class="jobs-table-cell jobs-table-title">职位名称</div>
            <div class="jobs-table-cell jobs-table-type">类别</div>
            <div class="jobs-table-cell jobs-table-salary">薪资</div>
            <div class="jobs-table-cell jobs-table-date">发布时间</div>
            <div class="jobs-table-cell jobs-table-action"></div>
          </div>
          <div
            v-for="(job, idx) in filteredJobs"
            :key="job.id"
            class="jobs-table-row"
            @click="showJobDetail(job)"
          >
            <div class="jobs-table-cell jobs-table-title">{{ job.title }}</div>
            <div class="jobs-table-cell jobs-table-type">{{ job.type }}</div>
            <div class="jobs-table-cell jobs-table-salary">{{ job.salary }}</div>
            <div class="jobs-table-cell jobs-table-date">{{ job.date }}</div>
            <div class="jobs-table-cell jobs-table-action">
              <span class="jobs-table-arrow">&gt;</span>
            </div>
          </div>
        </div>
        <!-- 弹窗 -->
        <div v-if="showDetail" class="job-modal-mask" @click.self="showDetail = false">
          <div class="job-modal">
            <div class="job-modal-header">
              <span class="job-modal-title">{{ selectedJob.title }}</span>
              <span class="job-modal-close" @click="showDetail = false">×</span>
            </div>
            <div class="job-modal-body">
              <div class="mb-2"><b>类别：</b>{{ selectedJob.type }}</div>
              <div class="mb-2"><b>薪资：</b>{{ selectedJob.salary }}</div>
              <div class="mb-2"><b>发布时间：</b>{{ selectedJob.date }}</div>
              <div class="mb-2"><b>工作地点：</b>{{ selectedJob.location }}</div>
              <div class="mb-2"><b>公司介绍：</b>紫燕无人机是一家专注于工业级无人机研发、生产与应用的高科技企业，致力于为测绘、农业、安防等行业提供高效智能的无人机解决方案。</div>
              <div class="mb-2"><b>岗位职责：</b>
                <ul>
                  <li v-for="(duty, i) in selectedJob.duties" :key="i">{{ duty }}</li>
                </ul>
              </div>
              <div class="mb-2"><b>任职要求：</b>
                <ul>
                  <li v-for="(req, i) in selectedJob.requirements" :key="i">{{ req }}</li>
                </ul>
              </div>
              <div class="mb-2"><b>福利待遇：</b>五险一金、带薪年假、节日福利、年度体检、团建活动、项目奖金等。</div>
              <div class="mb-2"><b>联系方式：</b>hr@ziyanuav.cn</div>
            </div>
          </div>
        </div>
        <!-- 应聘信息表单 -->
        <div class="apply-form-wrapper">
          <div class="apply-form-title">有意向请与我们联系</div>
          <form class="apply-form" @submit.prevent="submitApply">
            <div class="apply-form-row">
              <div class="apply-form-col">
                <input v-model="applyForm.name" type="text" class="apply-input" placeholder="姓名" required />
              </div>
              <div class="apply-form-col">
                <input v-model="applyForm.phone" type="text" class="apply-input" placeholder="联系电话" required />
              </div>
            </div>
            <div class="apply-form-row">
              <div class="apply-form-col">
                <input v-model="applyForm.position" type="text" class="apply-input" placeholder="岗位" required />
              </div>
              <div class="apply-form-col">
                <input v-model="applyForm.email" type="email" class="apply-input" placeholder="邮箱" required />
              </div>
            </div>
            <div class="apply-form-row">
              <div class="apply-form-col">
                <select v-model="applyForm.location" class="apply-input" required>
                  <option value="">工作地点</option>
                  <option>珠海</option>
                  <option>上海</option>
                  <option>深圳</option>
                  <option>广州</option>
                  <option>成都</option>
                  <option>其他</option>
                </select>
              </div>
              <div class="apply-form-col"></div>
            </div>
            <div class="apply-form-row">
              <button type="submit" class="apply-submit-btn">提交</button>
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'JobsView',
  data() {
    return {
      jobs: [
        {
          id: 1,
          title: '无人机系统工程师',
          type: '研发类',
          salary: '面议',
          date: '2025-06-18',
          location: '珠海',
          duties: [
            '负责无人机系统集成与测试',
            '参与无人机系统方案设计',
            '协助项目推进与技术支持'
          ],
          requirements: [
            '本科及以上学历，电子、自动化等相关专业',
            '有无人机相关经验优先',
            '具备良好的沟通与团队协作能力'
          ]
        },
        {
          id: 2,
          title: '无人机软件工程师',
          type: '研发类',
          salary: '面议',
          date: '2025-06-18',
          location: '珠海',
          duties: [
            '负责无人机软件开发与维护',
            '参与飞控系统开发',
            '优化软件性能与稳定性'
          ],
          requirements: [
            '本科及以上学历，计算机、自动化等相关专业',
            '熟悉C/C++/Python等编程语言',
            '有嵌入式开发经验优先'
          ]
        },
        {
          id: 3,
          title: '无人机设计工程师',
          type: '研发类',
          salary: '面议',
          date: '2025-06-18',
          location: '珠海',
          duties: [
            '负责无人机结构设计',
            '参与产品外观与功能设计',
            '协助生产工艺改进'
          ],
          requirements: [
            '本科及以上学历，机械设计等相关专业',
            '熟练使用CAD/SolidWorks等设计软件',
            '有无人机设计经验优先'
          ]
        },
        {
          id: 4,
          title: 'PCB设计工程师',
          type: '研发类',
          salary: '面议',
          date: '2025-06-18',
          location: '珠海',
          duties: [
            '负责PCB电路板设计',
            '参与硬件方案评审',
            '协助产品测试与调试'
          ],
          requirements: [
            '本科及以上学历，电子相关专业',
            '熟练使用Altium Designer等EDA工具',
            '有PCB设计经验优先'
          ]
        },
        {
          id: 5,
          title: '大客户销售经理',
          type: '销售类',
          salary: '20K-40K/月',
          date: '2025-06-18',
          location: '上海',
          duties: [
            '负责公司无人机产品的大客户销售与市场拓展',
            '维护与开发重点行业客户关系',
            '参与销售策略制定与执行'
          ],
          requirements: [
            '本科及以上学历，市场营销等相关专业',
            '3年以上大客户销售经验，有无人机或高科技行业经验优先',
            '具备良好的沟通与商务谈判能力'
          ]
        },
        {
          id: 6,
          title: '渠道销售专员',
          type: '销售类',
          salary: '10K-20K/月',
          date: '2025-06-18',
          location: '深圳',
          duties: [
            '负责区域渠道的开发与维护',
            '协助完成销售目标',
            '收集市场信息，反馈客户需求'
          ],
          requirements: [
            '大专及以上学历，市场营销等相关专业',
            '有渠道销售经验优先',
            '具备较强的执行力和团队合作精神'
          ]
        },
        {
          id: 7,
          title: '客户服务专员',
          type: '客户类',
          salary: '8K-15K/月',
          date: '2025-06-18',
          location: '广州',
          duties: [
            '负责客户售前、售中、售后服务支持',
            '解答客户咨询，处理客户投诉',
            '协助客户培训与技术支持'
          ],
          requirements: [
            '大专及以上学历，专业不限',
            '有客户服务或技术支持经验优先',
            '具备良好的服务意识和沟通能力'
          ]
        },
        {
          id: 8,
          title: '售后技术支持工程师',
          type: '客户类',
          salary: '10K-18K/月',
          date: '2025-06-18',
          location: '成都',
          duties: [
            '负责无人机产品的售后技术支持',
            '现场故障排查与维修',
            '客户技术培训与指导'
          ],
          requirements: [
            '大专及以上学历，电子、自动化等相关专业',
            '有无人机或相关产品售后经验优先',
            '能适应短期出差'
          ]
        }
      ],
      showDetail: false,
      selectedJob: {},
      search: '',
      searchKey: '',
      category: '',
      applyForm: {
        name: '',
        phone: '',
        position: '',
        email: '',
        location: ''
      }
    }
  },
  methods: {
    showJobDetail(job) {
      this.selectedJob = job;
      this.showDetail = true;
    },
    doSearch() {
      this.searchKey = this.search.trim();
    },
    submitApply() {
      // 简单校验
      if (!this.applyForm.name || !this.applyForm.phone || !this.applyForm.position || !this.applyForm.email || !this.applyForm.location) {
        alert('请完整填写所有信息');
        return;
      }
      // 生成邮件内容
      const subject = encodeURIComponent('应聘职位：' + this.applyForm.position);
      const body = encodeURIComponent(
        `姓名：${this.applyForm.name}\n联系电话：${this.applyForm.phone}\n岗位：${this.applyForm.position}\n邮箱：${this.applyForm.email}\n工作地点：${this.applyForm.location}`
      );
      window.location.href = `mailto:hr@ziyanuav.cn?subject=${subject}&body=${body}`;
    }
  },
  computed: {
    filteredJobs() {
      let jobs = this.jobs;
      if (this.category) {
        jobs = jobs.filter(job => job.type === this.category);
      }
      if (!this.searchKey) return jobs;
      const key = this.searchKey.toLowerCase();
      return jobs.filter(job =>
        job.title.toLowerCase().includes(key) ||
        job.type.toLowerCase().includes(key)
      );
    }
  }
}
</script>

