  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  overflow: hidden;
  margin-bottom: 40px;
}
.jobs-table-header, .jobs-table-row {
  display: flex;
  align-items: center;
  min-height: 60px;
  border-bottom: 1px solid #eee;
  font-size: 1.08rem;
}
