  flex-direction: column;
}
.apply-input {
  height: 44px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 0 16px;
  font-size: 1.08rem;
  margin-bottom: 0;
  background: #fafbfc;
}
.apply-form-row:nth-child(2) .apply-input {
  height: 44px;
}
.apply-form-row:nth-child(3) .apply-input {
  height: 44px;
}
.apply-submit-btn {
  width: 180px;
